.page {
  font-family: "Cinzel", sans-serif;
  scroll-behavior: smooth;

  &__body {
    margin: 0;
    background: $c-gray;

    &--with-menu {
      overflow: hidden;
    }
  }

  &__menu {
    position: fixed;
    top: 0;
    right: 0;
    left: 0;

    transform: translateX(-100%);
    transition: transform 0.3s;

    &:target {
      transform: translateX(0);
    }
  }

  &__section {
    margin-bottom: 148px;

    @include onMobile {
      margin-bottom: 120px;
    }

    &-title {
      @extend %title-1;
      text-align: center;
      margin-bottom: 40px;

      @include onDesktop {
        margin-bottom: 48px;
      }
    }
  }
}
